import React, {Component, Fragment} from 'react';
import PropTypes from 'prop-types';
//引入组件
import styles from 'react-responsive-carousel/lib/styles/carousel.min.css';
import {Carousel as Swiper} from 'react-responsive-carousel';
import recommendPic from '../../../common/image/pic.png';

class Carousel extends Component {
    constructor(props) {
        super(props);
        this.state = {
            imgArr: [{0: ''}, {1: ''}, {2: ''}, {3: ''}, {4: ''}]//偷
        }
    }

    static propTypes = {
        slider: PropTypes.array.isRequired
    };
    carouselOptions = {
        showArrows: false,
        showStatus: false,
        showThumbs: false,
        infiniteLoop: true
    };
  /*  state = {
        imgArr: []
    }
    imgOnload = (e, index) => {
        e.target.parentNode.removeChild(e.target);
        this.setState({
            imgArr: this.state.imgArr.map((item, i) => {
                if (i === index) {
                    item = e.target.src
                }
                return item
            })
        })
    }
*/
    carouselList = (data) => {
        return (
            <Swiper key='Swiper' styles={styles} {...this.carouselOptions}>
                {data.map((list, index) => {
                    return (
                        <Fragment key={index}>
                            {/*<img onLoad={e => this.imgOnload(e, index)} style={{display: 'none'}} src={list.picUrl}
                                 alt=""/>
                            <img src={this.state.imgArr[index] ? this.state.imgArr[index] : recommendPic} alt=''/>*/}
                            <img src={list.picUrl} alt=""/>
                        </Fragment>
                    )
                })}
            </Swiper>
        )
    };

    render() {
        return (
            this.carouselList(this.props.slider)
        )
    }
}

export default Carousel

