import {recommendApi} from '../api/recommend'

export const recommendAction = () => {
    return dispatch => {
        return recommendApi().then(
            res => {
                if (res.code === 0) {
                    return dispatch({type: 'recommendList', data: res.data})
                } else {
                    return dispatch({type: 'recommendError'})
                }
            },
            ({response}) => {
                dispatch({type: 'recommendError'})
            }
        )
    }
}

