import React, {Component, Fragment} from 'react';
import './Radio.styl';
import PropTypes from "prop-types";
import recommendPic from "../../../common/image/pic1.png";

class Radio extends Component {
    static propTypes = {
        radio: PropTypes.array.isRequired
    }

   /* constructor(props) {
        super(props);
        this.state = {
            imgArr: [
                {0: ''}, {1: ""}//偷
            ]
        }
    }

    imgOnload = (e, index) => {
        e.target.parentNode.removeChild(e.target);
        this.setState({
            imgArr: this.state.imgArr.map((item, i) => {
                if (i === index) {
                    item = e.target.src
                }
                return item
            })
        })
    }*/
    radioList = (data) => {
        if (data.length === 0) {
            return null
        } else {
            return (
                <Fragment>
                    <div className='radio_title'>电台</div>
                    <div className='radio_items'>
                        {data.map((list, index) => {
                            return (
                                <div className='radio_item' key={list.radioid}>
                                    {/*<img onLoad={e => this.imgOnload(e, index)} style={{display: 'none'}}
                                         src={list.picUrl} alt=""/>
                                    <img src={this.state.imgArr[index] ? this.state.imgArr[index] : recommendPic}
                                         alt=''/>*/}
                                    <img src={list.picUrl} alt=""/>
                                    <div>{list.Ftitle}</div>
                                </div>)
                        })}
                    </div>
                </Fragment>
            )
        }
    };

    render() {
        return this.radioList(this.props.radio)
    }
}

export default Radio
