import React, {Component, Fragment} from 'react';
import './SongMenu.styl';
import PropTypes from "prop-types";
import {connect} from 'react-redux';
import {withRouter} from 'react-router-dom';
import {hotSongAction} from '../../../action/hotSong';
import {listenFormat} from "../../../common/js/format";
import headsetLogo from '../../../common/image/headset.svg';
import userLogo from '../../../common/image/user.svg';
import recommendPic from "../../../common/image/pic1.png";

class SongMenu extends Component {
    static propTypes = {
        songList: PropTypes.array.isRequired,
        hotSongAction: PropTypes.func.isRequired,
    };

 /*   constructor(props) {
        super(props);
        this.state = {
            imgArr: [{0: ''}, {1: ''}, {2: ''}, {3: ''}, {4: ''}, {5: ''}]//
        }
    }

   imgOnload = (e, index) => {
        e.target.parentNode.removeChild(e.target);
        this.setState({
            imgArr: this.state.imgArr.map((item, i) => {
                if (i === index) {
                    item = e.target.src
                }
                return item
            })
        })
    }*/
    hotSong = (index) => {
        this.props.hotSongAction(index).then(res => {
            if (res) {
                this.props.history.push('/Hot?index=' + index)
            } else {
                return null
            }
        })
    }
    songList = (data) => {
        if (data.length === 0) {
            return null
        } else {
            return (
                <Fragment>
                    <div className='song_title'>热门歌单</div>
                    <div className='song_items'>
                        {data.map((list, index) => {
                            return (
                                <div className='song_item' onClick={() => this.hotSong(index)} key={list.id}>
                                    <div ref='song_img' className='song_img'>
                                        {/*<img onLoad={e => this.imgOnload(e, index)} style={{display: 'none'}}
                                             src={list.picUrl} alt=' '/>
                                        <img src={this.state.imgArr[index] ? this.state.imgArr[index] : recommendPic}
                                             alt=' '/>*/}
                                        <img src={list.picUrl} alt=""/>
                                        <div className='song_headset'>
                                            <img src={headsetLogo} alt="headset"/>
                                            <span>{listenFormat(list.accessnum)}</span>
                                        </div>
                                    </div>
                                    <div className='song_info'>
                                        <div className='song_desc'>{list.songListDesc}</div>
                                        <div className='song_auth'>
                                            <img className='user_img' src={userLogo} alt='auth'/>
                                            <div
                                                className='user_name'>{list.songListAuthor.length >= 14 ? "获取用户名失败" : list.songListAuthor}</div>
                                        </div>
                                    </div>
                                </div>
                            )
                        })}
                    </div>
                </Fragment>
            )
        }
    };

    render() {
        return this.songList(this.props.songList)
    }
}


export default connect(null, {hotSongAction})(withRouter(SongMenu))
