import React, {Component} from 'react';
import './Recommend.styl';
//redux
import {connect} from 'react-redux';
import {recommendAction} from '../../action/recommendAction';
//引入组件
import Carousel from './Carousel/Carousel';
import Radio from './Radio/Radio';
import SongMenu from './SongMenu/SongMenu';
import Loading from '../common/Loading/Loading';
import PropTypes from "prop-types";
import Scroll from '../common/Scroll/Scroll';

class Recommend extends Component {
    static propTypes = {
        recommendStore: PropTypes.object.isRequired,
        scrollStore: PropTypes.object.isRequired,
        playerStore: PropTypes.object.isRequired,
        recommendAction: PropTypes.func.isRequired,
    };

    componentWillMount() {
        this.props.recommendAction()
    }
    display = (recommendStore) => {
        if (recommendStore.flag) {
            return (
                <Scroll
                    key='recommendScroll'
                    top={'5.2rem'}
                    bottom={!this.props.playerStore.smallPlayer ? 0 : '10vh'}
                    scrollName='recommendScroll'
                >
                    <Carousel  slider={recommendStore.slider}/>
                    <Radio radio={recommendStore.radioList}/>
                    <SongMenu songList={recommendStore.songList}/>
                </Scroll>
            )
        } else {
            return <Loading/>
        }
    };

    render() {
        const {recommendStore} = this.props;
        return this.display(recommendStore)
    }
}

const mapStateToProps = (state) => {
    return {
        scrollStore: state.scrollStore,
        recommendStore: state.recommendStore,
        playerStore: state.playerStore
    }
};

export default connect(mapStateToProps, {recommendAction})(Recommend)
