import axios from 'axios'
export const hotSongAction=(index)=>{
    return dispatch=>{
        return axios.get('/api/hot?index='+index).then(res=>{
            if(res.data.code===0){
                dispatch({type:'hotSong',data:res.data.req_0.data})
                return true
            }else{
                return false
            }
        })
    }
}
export const hotSongLoadingAction=(index,total)=>{
    let date=new Date().getTime()
    return dispatch=>{
        const value = {
            _: date,
            g_tk: 1580658300,
            uin: 822059927,
            format: 'json',
            inCharset: 'utf-8',
            outCharset: 'utf-8',
            notice: 0,
            platform: 'h5',
            needNewCode: 1,
            new_format: 1,
            pic: 500,
            disstid: index,
            type: 1,
            json: 1,
            utf8: 1,
            onlysong: 0,
            nosign: 1,
            song_begin: total,
            song_num: 15
        };
        return axios.get('/api/hotSongGet', {
           params: value
        }).then(res=>{
            dispatch({type:'hotSongLoading',data:res.data.cdlist})
        })
    }

}
