import Jsonp from '../common/js/myJsonp';


export const recommendApi = () => {
    const url = 'https://c.y.qq.com/musichall/fcgi-bin/fcg_yqqhomepagerecommend.fcg?';
    const Options = {
        param: 'jsonpCallback'
    };
    const data = {
        g_tk: 5381,
        uin: 0,
        format: 'json',
        inCharset: 'utf-8',
        outCharset: 'utf-8',
        notice: 0,
        platform: 'h5',
        needNewCode: 1,
        _: 1544528286930
    }
    return Jsonp(url, data, Options)
}
